.app-wrapper{
  margin: 0 auto;
  display: grid;
  width: 1200px;

  grid-template-areas:
"h h"
"n c";

  grid-template-rows: 60px 1fr;
  grid-auto-columns: 2fr 10fr;
  /*grid-gap: 10px;*/

}

.header {
grid-area: h;
  background-color: greenyellow;

}

.header img {
  width: 60px;
}

.nav {
grid-area: n;
  background-color: gold;
}

.content {
  grid-area: c;
  background-color: cornflowerblue;
}